<template lang="pug">
    div.content-article-index
        iframe(frameborder=0 :width="w_w" :height="w_h" :src="`../../wap/index.php?ctl=settings&act=article_show&cate_id=${cate_id}`")
</template>
<script>
export default {
    name: 'settingArticleIndex',
    data() {
        return {
            cate_id: '',
            w_w: '',
            w_h: ''
        }
    },
    created() {
        this.cate_id = this.$route.query.id;
    },
    mounted() {
        this.w_w = $(window).width();
        this.w_h = $(window).height();
        document.title = this.$route.query.title;
    }
}
</script>



